@import '../../../scss/mixins';

.main-sidebar .nav .nav-item.active>.nav-link {
    background-color: #fff;
    color: #707070;
    font-size: 14px;
    font-weight: 500;
}

.disabled {
    pointer-events: none;
}

.hoverclass:hover {
    background: #D7F5FF;
    color: #197D9F !important;
    font-size: 14px;
    font-weight: 500;

}

.main-sidebar .nav .nav-item .nav-link {
    color: #202020 !important;
    font-size: 16px;
    font-weight: 300;
    height: 40px;
    margin: 3px 0px;
    display: flex;
    margin: 3px 13px;
    border-radius: 12px;
    align-items: center;

    & i {
        color: #898989;
        font-size: 18px;
    }

    & i.sidenv {
        display: none;
    }


}

