a.col_side {
    color: #202020 !important;
    font-size: 16px;
    font-weight: 300;
    height: 40px;
    display: flex;
    margin: 3px 14px;
    border-radius: 12px;
    align-items: center;
    padding: 0px 15px;
    justify-content: space-between;
    width: auto;
}

a.col_side:hover {
    color: #000 !important;
    background: #0065FF26;
    font-size: 16px;
    font-weight: 300;
    display: flex;
    height: 40px;
    margin: 3px 14px;
    border-radius: 12px;
    align-items: center;
    padding: 0px 15px;
    text-decoration: none;

    & i {
        color: #898989;
    }

    &.card.card-body.sides {
        border: none;
        visibility: hidden;
    }
}


.hoverclass {
    &.nobg {
        background: none !important;
    }
}

.submenu {
    display: none;
}

.active>.submenu {
    display: block;
}

.submenu>*>.hoverclass {
    padding-left: 25px;


}

.submenu.child2>*>.hoverclass {
    padding-left: 35px;
}

span.side_head {
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
}

.card.card-body.sides {
    // margin:6px;
    padding: 0px 0px;
}

.card.card-body.sides li.nav-item.active a span {
    color: #765aa6 !important;
    font-weight: 300 !important;
}

.card.card-body.sides li a {
    border-radius: 4px !important;
    padding-left: 42px;
}

.card.card-body.sides {
    border: none;
}

.card.card-body.sides li a span {
    color: #5A5A5A !important;
    white-space: nowrap;
    // font-weight: 100;
    font-weight: 400;
    font-size: 14px;
    max-width: 165px;
}

span.side_icon {
    position: relative;
    right: -10px;
    top: 3px;
    color: #898989;
}

